<template>
	<view class="">
		<navbar :leftIconBool='false' :rightBool='false' :Navtitle='Navtitle' @back='back' :navBoolean='false'></navbar>
		<publicModel :modelData='modelData' @shareModel='shareJs'></publicModel>
		<view class="" style="padding: 30rpx 30rpx;margin-top: 30rpx;background-color: #FFFFFF;display: flex;justify-content: space-between;align-items: center;">
			<text>分类</text>
			<view class="" style="" @click="editType">
				<text>{{modelData.modelCategoryName}}</text>
				<uni-icons type="arrowright"></uni-icons>
			</view>
		</view>
		<view class="" style="padding: 30rpx 30rpx;margin-top: 30rpx;background-color: #FFFFFF;display: flex;justify-content: space-between;align-items: center;">
			<text>是否公开</text>
			<view class="" style="">
				<u-switch v-model="checked" :disabled="disabled" active-color="#86B0D4" @change="changeSwitch"></u-switch>
			</view>
		</view>
		<view class="emptyDiv">
		</view>
		<view class="footer">
			<modelBottom style='width: 100%;' @show='show'></modelBottom>
		</view>
		<u-modal v-model="addshow" width='650' title="模型分类" confirm-color='#000000' negative-top='140rpx' :show-cancel-button='true' ref="uModal" @confirm="confirm" @cancel="cancel">
			<view class="slot-content">
				<scroll-view scroll-y="true" style="max-height:360rpx ;height: 100%;">
					<u-radio-group class="radioGroup" v-model="value" active-color='#86B0D4'>
						<view class="radioItem" style="" v-for="(item, index) in tabs" :key="index" >
							{{item.modelLibTypeName}}
							<u-radio
								icon-size='30'
								@change="radiochange($event,item.id)"
								:name="item.modelLibTypeName"
							>
							</u-radio>
						</view>
					</u-radio-group>
				</scroll-view>
			</view>
		</u-modal>
		<!-- vr弹窗显示 -->
		<vrshow ref='SonVrshow' :deviceGroup='deviceGroup' :threeDmodelId='modelData.fileModelId' threeDmodelName='ModelLib'></vrshow>
		<!-- 微信的分享 popup -->
		<wechatShare ref='sonWechatShare' @shareWeixin='shareWeixin' @shareFriend='shareFriend' @cancelshare='cancelshare'></wechatShare>
		<u-no-network></u-no-network>
	</view>
</template>

<script>
	import {querydevice} from '@/utill/api/DeviceGroup/querydevice.js'
	import {getModelDetail,editModel,getClassifyType,batchshareModel} from '@/utill/api/model/getModel.js'
	import modelBottom from '@/components/model/modelBottom.vue'
	import publicModel from '@/components/model/publicModel.vue'
	export default{
		components:{
			//模型库的底部功能
			modelBottom,
			publicModel
		},
		data(){
			return{
				Navtitle:'模型库详情',
				addshow:false,
				modelData:{
					previewImage:{}
				},
				checked: false,
				disabled:false,
				tabs:[],
				value:'',
				modelCategoryId:'',//分类id
				modelId:'',//模型库id
				picUrl:uni.getStorageSync('uploadLogo'),//分享的封面图
				weixinUrl:'',//3js与页面统一的路径
				deviceGroup:[],//设备组的数据
				pageUrl:'',//页面路由
				paramter:'',//页面参数
				shareTitle:'模型名称',
				shareContent:'模型介绍',
			}
		},
		methods:{
			back(){
				// #ifdef H5
				uni.showToast({
					title:'网页端无效',
					icon:'none'
				})
				// #endif
				// #ifdef APP-PLUS||MP-WEIXIN
					uni.navigateBack({
						delta:1
					})
				// #endif
			},
			// 修改分类
			editType(){
				// #ifdef H5
				uni.showToast({
					title:'网页端不能操作',
					icon:'none'
				})
				// #endif
				// #ifdef APP-PLUS||MP-WEIXIN
				 this.getClassifyType()
				// #endif
			},
			radiochange(e,id){
				console.log(e)
				console.log(id)
				this.modelCategoryId=id
			},
			confirm(){
				console.log(this.modelCategoryId)
				editModel({
					ownId:uni.getStorageSync('doctorId'),
					id:this.modelId,
					modelCategory:this.modelCategoryId
				}).then(res=>{
					console.log(res)
					if(res.data.code==0){
						uni.showToast({
							title:res.data.msg,
							icon:'none'
						})
					}else{
						uni.showToast({
							title:res.data.msg,
							icon:'none'
						})
					}
				}).catch(err=>{
					console.log(err)
				})
			},
			cancel(){},
			getClassifyType(){
				getClassifyType({}).then(res=>{
					console.log(res)
					this.tabs=res.data.object
					this.addshow=true
					console.log(this.modelCategoryId)
					this.tabs.filter(item=>{
						if(item.id==this.modelCategoryId){
							this.value=item.modelLibTypeName
						}
					})
				}).catch(err=>{
					console.log(err)
				})
			},
			changeSwitch(value){
				// console.log(value)
				// #ifdef H5
				uni.showToast({
					title:'网页端不能做修改',
					icon:'none'
				})
				// #endif
				// #ifdef APP-PLUS||MP-WEIXIN
					batchshareModel({
						ids:this.modelId,
						share:value,
					}).then(res=>{
						console.log(res)
						if(res.data.code==0){
							
						}else{
							uni.showToast({
								title:res.data.msg,
								icon:'none'
							})
						}
					}).catch(err=>{
						console.log(err)
					})
				// #endif
			},
			// 获取模型库数据详情
			getDetail(id){
				var timer = setTimeout(function() {
					uni.showLoading({
					  title: '数据加载中...',
					})
				}, 2000)
				getModelDetail({
					id:id
				}).then(res=>{
					clearTimeout(timer)
					uni.hideLoading()
					console.log(res)
					if(res.data.code==0){
						this.Navtitle=res.data.object.modelName
						// uni.setNavigationBarTitle({
						// 	title:res.data.object.modelName
						// })
						this.modelData=res.data.object
						this.modelCategoryId=res.data.object.modelCategory
						this.checked=res.data.object.share//是否公开的开关
					}else{
						uni.showToast({
							title:res.data.msg,
							icon:'none'
						})
					}
				}).catch(err=>{
					clearTimeout(timer)
					uni.hideLoading()
					uni.showToast({
						title:'err',
						icon:'none'
					})
				})
			},
			show(index){
				switch (index){//xr显示
					case 0:
						console.log('0')
						if(!this.modelData.fileModelId){
							uni.showToast({
								title:'文件不存在',
								icon:'none'
							})
							return;
						}
						// 查询所有的设备组
						querydevice({}).then(res=>{
							console.log(res)
							if(res.data.code==0){
								this.deviceGroup=res.data.object
								this.$refs.SonVrshow.open()//调用子组件的打开方法，props传值，点击mask时会报错
							}else{
								uni.showToast({
									title:res.data.msg,
									icon:'none'
								})
							}
						}).catch(err=>{
							console.log(err)
						})
					break;
					case 1://三维显示
						console.log(this.modelData.fileModelId)
						if(!this.modelData.fileModelId){
							uni.showToast({
								title:'文件不存在',
								icon:'none'
							})
							return;
						}
						var modelUrl=this.$JslineUrl+'&userType=D'+'&token='+'&modelType=模型库'+'&titlename='+this.modelData.modelName+'&fileId='+this.modelData.fileModelId
						// console.log(modelUrl)
						uni.navigateTo({
							url:'/pages/ThreejsShow/ThreejsShow?baseUrl='+encodeURIComponent(modelUrl),
							animationType:'pop-in'
						})
					break;
					case 2://转发
						 let curRoute  = this.$mp.page.route;
						 // console.log(this.$mp.page.options)
						 this.pageUrl=curRoute
						 this.paramter=this.$mp.page.options.modelId
						 console.log(this.pageUrl)
						 console.log(this.paramter)
						 this.weixinUrl=this.$onlineUrl+this.pageUrl+'?modelId='+this.paramter
						 if(this.modelData.modelName){
							 this.shareTitle=this.modelData.modelName
						 }
						 this.shareContent='所属分类:'+this.modelData.modelCategoryName+'\n'+'创立人:'+this.modelData.ownerName
						 if(this.modelData.previewImage){
							 this.picUrl=Object.values(this.modelData.previewImage)[0] 
						 }
						 this.$refs.sonWechatShare.open()//popup展示
					break;
					default:
					break;
				}
			},
			// 取消分享
			cancelshare(){
				this.$refs.sonWechatShare.close()
			},
			// 分享三维模型到微信
			shareJs(){
				if(this.modelData.modelName){
					this.shareTitle=this.modelData.modelName+'的模型'
				}
				this.shareContent='所属分类:'+this.modelData.modelCategoryName+'\n'+'创立人:'+this.modelData.ownerName
				if(this.modelData.previewImage){
					this.picUrl=Object.values(this.modelData.previewImage)[0] 
				}
				this.weixinUrl=this.$JslineUrl+'&userType=D'+'&token='+'&modelType=模型库'+'&titlename='+this.modelData.modelName+'&fileId='+this.modelData.fileModelId
				// #ifdef APP-PLUS
				 this.$refs.sonWechatShare.open()
				// #endif
			},
			// 分享微信
			shareWeixin(){
				console.log(this.pageUrl)
				console.log(this.paramter)
				console.log(this.shareTitle)//模型的名称
				console.log(this.shareContent)//模型介绍
				// console.log(this.$JslineUrl+'&userType=D'+'&token='+'&modelType=手术方法库'+'&titlename='+this.arrData.surgicalPlanName+'&fileId='+this.stlId)
				uni.share({
					provider:'weixin',
					title:this.shareTitle,
					type:0,
					// href: this.$JslineUrl+'&userType=D'+'&token='+'&modelType=手术方法库'+'&titlename='+this.arrData.surgicalPlanName+'&fileId='+this.stlId,
					href: this.weixinUrl,
					summary:this.shareContent,
					scene:'WXSceneSession',
					imageUrl:this.picUrl,
					success(e) {
						uni.showToast({
							title:'分享成功',
							icon:'none'
						})
						console.log(e)	
					},
					fail(e) {
						uni.showToast({
							title:'分享失败',
							icon:'none'
						})
						console.log(e)
					}
				})
			},
			// 分享朋友圈
			shareFriend(){
				uni.share({
					provider:'weixin',
					title:this.shareTitle,
					type:0,
					href: this.weixinUrl,
					// href: this.$JslineUrl+'&userType=D'+'&token='+'&modelType=手术方法库'+'&titlename='+this.arrData.surgicalPlanName+'&fileId='+this.stlId,
					summary:this.shareContent,
					scene:'WXSenceTimeline',
					imageUrl:this.picUrl,
					success(e) {
						uni.showToast({
							title:'分享成功',
							icon:'none'
						})
						console.log(e)
					},
					fail(e) {
						uni.showToast({
							title:'分享失败',
							icon:'none'
						})
						console.log(e)
					}
				})
			},
		},
		onLoad(options) {
			console.log(options)
			this.modelId=options.modelId
			this.getDetail(options.modelId)
		},
		onShow() {
			// #ifdef H5
			// 在网页端,是否公开按钮要禁用
			this.disabled=true
			// #endif
		},
		onNavigationBarButtonTap(e){
			console.log(e)
			// #ifdef H5
			uni.showToast({
				title:'网页端不能操作',
				icon:'none'
			})
			// #endif
			// #ifdef APP-PLUS
			 this.show(2)
			// #endif
		},
		onBackPress(e) {
			// console.log(e)
			// h5页面不需要返回首页
			// #ifdef H5
				return true
			// #endif
		},
	}
</script>

<style scoped lang="scss">
	.footer{
		height:110rpx;
		border-top:1px solid #EEEEEE;
		box-sizing: border-box;
		display: flex;
		align-items: center;
		justify-content: space-between;
		padding: 0 30rpx;
		background: #FFFFFF;
		position: fixed;
		width: 100%;
		z-index: 2020;
		bottom: calc(var(--window-bottom))
	}
	.emptyDiv{
		height: 110rpx;
		width: 100%;
	}
	.slot-content{
		padding: 40rpx 40rpx;
		height: 100%;
	}
	.radioGroup{
		padding: 10rpx 0;
		overflow: auto;
		width: 100%;
	}
	.radioItem{
		width: 100%;
		display: flex;
		justify-content: space-between;
		line-height: 56rpx;
		color: #000000;
		font-size: 28rpx;
		font-family:PingFang-SC-Regular;
	}
</style>
